.ticker-section{
  padding-top: 24px;

  @include respond-to(sm){
    padding-top: 40px;
  }
  
  h2{
    font-family: 'mabry-medium';
    font-size: 40px;
    font-weight: 400;

    @include respond-to(sm){
      font-size: 74px;
      margin-top: 20px;
    }
  }

  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  &__copy{
    text-align: center;
    padding-bottom: 31px;
    padding-top: 25px;

    @include respond-to(sm){
      padding-bottom: 70px;
    }
  }

  .marquee {
    position: relative;
    width: 100vw;
    max-width: 100%;
    height: 95px;
    overflow-x: hidden;

    @include respond-to(sm){
      height: 198px;
    }
  }
  
  .track {
    position: absolute;
    white-space: nowrap;
    //will-change: transform;
    transition-timing-function: linear !important;
    transition: 83s;
  //   animation: marquee 10s linear infinite;
  //   -webkit-animation: marqueeweb 10s linear infinite;
   }
  
  // @keyframes marquee {
  //   from { transform: translateX(0); }
  //   to { transform: translateX(-100%); }
  // }

  // @-webkit-keyframes marqueeweb {
  //   from { -webkit-transform: translateX(0); }
  //   to { -webkit-transform: translateX(-100%); }
  // }

  .btn{

    margin-bottom: 41px;
    @include respond-to(sm){
      width: 523px;
      margin-bottom: 83px;
    }
  }

  &--dark{
    background-color: $brand-color;
    color: white;

    svg{
      circle, path, line{
        stroke: white;
      }
    }

    .btn{
      border: 1px solid white;
      color: white;
  
      @include hover{
        background-color: white;
        color: $brand-color;
      }
  
      &.active{
        background-color: white !important;
        color: $brand-color !important;
      }
    }
  }
}