// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------
.header{
  z-index: 999;
  top: 28px;
  position: fixed;
  width: 100%;

  &.active{
    top: 0px;

    svg{
      height: 40px;
      margin: 15px 0px;
    }
  }

  &__main{
    border-width: 1px;
    z-index: 999;
    background-color: white;
    position: relative;
  }

  &__main-inner{
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;

    @include respond-to(md){
      justify-content: center;
    }
  }

  &__hamburger{
    position: absolute;
    left: 0;
    padding-left: 20px;
    //top: 15px;

    &.active{
      .header__hambuger-line{
        margin: 0;
      }     

      .header__hambuger-line:first-of-type{
        transform: rotate(45deg);
      }

      .header__hambuger-line:nth-of-type(2){
        display: none;
      }

      .header__hambuger-line:last-of-type{
        transform: rotate(-45deg);
      }      
    }
  }

  &__hambuger-line{
    width: 30px;
    height: 1px;
    background-color: #00205c;
    margin: 8px 0px;
    transform-origin: center;
    transition: 0.4s;

    @include respond-to(md){
      display: none;
    }
  }

  &__main-nav{
    position: absolute;
    right: 20px;
    top: 17px;


    @include respond-to(md){
      display: block;
      position: absolute;
      right: 0;
      top: 24px;
      padding-right: 20px;
    }

    ul{
      display: flex;
    }

    li:first-child{
      display: none;

      @include respond-to(sm){
        display: block;
      }


    }



    li{
      font-size: 12px;
      margin-left: 46px;
      letter-spacing: 1.6px;
      font-family: 'mabry-light';

      @include hover{
        cursor: pointer;
      }


      @include respond-to(sm){
        font-size: 16px;
      }
    }
  }

  &__logo {   

    svg{
      margin: 17px 0px;
      height: 40px;
      transition: 0.4s;

      @include respond-to(md){
        height: 75px;
        margin: 14px 0px;
      }
    }    
  }

  &__nav{
    display: none;

    @include respond-to(md){
      display: block;
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: center;
      border-width: 1px;
      background-color: white;
      transform: translateY(-100%);
      transition: 0.4s;

      &.active{
        transform: translateY(0%);
      }
    }

    ul{
      display: flex;
      width: 100%;
      justify-content: center;

      li{
        //padding: 15px 29px;
        padding: 8px 29px 13px;
      }

      a{
        font-size: 20px;
        letter-spacing: 2px;
        font-family: 'mabry-medium';
        text-transform: uppercase;
      }
    }
  }

  &__mobile-top{
    display: flex;
    padding: 17px 20px;
    align-items: center;
    border-bottom: solid white;
    border-width: 1px;
    justify-content: space-between;

    svg{
      height: 40px;
      width: 117px;

      path{
        fill: white;
      }
    }
  }

  &__mobile-top-right{
    display: flex;
    align-items: center;

    li{
      list-style: none;
      font-family: 'mabry-light';
      font-size: 12px;
    }
  }

  &__mobile-close-icon{
    width: 22px;
    height: 2px;
    background-color: white;
    margin-left: 19px;
  }

  &__mobile{
    padding-bottom: 40px;
    transform: translateX(-150%);
    z-index: 99;
    transition: 0.4s;
    background-color: $brand-color;
    color: white;
    position: absolute;
    width: 100%;
    top: 0px;
    z-index: 999999;

    #menu-mobile-below-divider{
      margin-top: 34px;
      padding-top: 40px;
      border-top: dashed #fff;
      border-width: 1px;
    }

    #menu-mobile-above-diveder{
      margin-top: 34px;
    }


    &.active{
      transform: translateX(0%);
      overflow: scroll;
      height: 100vh;
    }

    ul{
      width: 100%;

      li{
        padding: 10px 20px;
      }

      a, li{
        font-size: 20px;
        letter-spacing: 2px;
        font-family: 'mabry-medium';
        text-transform: uppercase;
        color: white;
      }
    }
  }

  .animated-border--desktop-only{
    display: none;

    @include respond-to(md){
      display: block;
    }
  }
}

.current-menu-item{
  opacity: 0.7;
}

.js-shop{
  @include hover{
    cursor: pointer;
    opacity: 0.4;
  }
}

.loader-js-add-to-cart{
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: #00000024;
  z-index: 99999999;
  transform: translateX(-200%);
  top: 0px;

  &.active{
    transform: translateX(0);
  }
}

@media screen and (max-width: 995px), 
       screen and (max-height: 450px) {
.cart-fly__subscription-info{
  display: none !important;
}

}